<template>
  <div class="about">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-table :data="items" size="mini">
      <el-table-column prop="_id" label="ID" width="230"> </el-table-column>
      <el-table-column prop="parent.name" label=""> </el-table-column>
      <el-table-column prop="name" label=""> </el-table-column>
      <el-table-column fixed="right" label="" width="180">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            @click="$router.push(`/categories/edit/${scope.row._id}`)"
            ></el-button
          >
          <el-button type="primary" size="mini" @click="remove(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: []
    }
  },
  methods: {
    async fetch() {
      // console.log('fetchstart')
      const res = await this.$http.get('rest/categories')
      this.items = res.data
      // console.dir(this.items)

      // console.log('fetchend')
    },
    async remove(row) {
      this.$confirm(` "${row.name}"`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        await this.$http.delete(`rest/categories/${row._id}`)
        this.$message({
          type: 'success',
          message: '!'
        })
        this.fetch()
      })
    }
  },
  created() {
    // console.log('created')
    this.fetch()
  }
}
</script>
